/* Styles/VerifyEmail.css */

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  background-color: #f5f5f5;
  color: #333;
}

.verify-email-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #ffffff;
  padding: 2rem;
}

.verify-email-container h2 {
  color: #6a1b9a;
}

.verify-email-container p {
  margin-bottom: 2rem;
}

.verify-email-container button {
  padding: 0.75rem 1.5rem;
  background-color: #8e24aa;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.verify-email-container button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

.verify-email-container button:hover:not(:disabled) {
  background-color: #6a1b9a;
}

.loading-logo {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3;
  border-top: 5px solid #6a1b9a;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-top: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
